ol,
ul {
  margin: 0;
  padding: 0;
}

picture {
  display: block;
}

table {
  table-layout: fixed;
}

h1 {
  font-size: 30px;
  font-weight: $fw-medium;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  @include media-breakpoint-down(xs) {
    font-size: 17px;
  }
}

h2 {
  font-size: 25px;
  font-weight: $fw-medium;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  @include media-breakpoint-down(xs) {
    font-size: 16px;
  }
}

h3 {
  font-size: 20px;
  font-weight: $fw-medium;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  @include media-breakpoint-down(xs) {
    font-size: 13px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}