.header {
  padding: 130px 0;
  background: url(http://max.montetuning.com/rschip/bg-offer.jpg) no-repeat 50% 50% / cover;
  @include media-breakpoint-down(xs) {
   padding: 35px 0;
    background: url(http://max.montetuning.com/rschip/bg-offer.jpg) no-repeat 64% 47%/cover;
  }

  .logo {
    text-align: left;
    svg {
      width: 195px;
      height: 24px;
      @include media-breakpoint-down(xs) {
        width: 140px;
        height: 17px;
      }
    }
  }
}