.wrap-site {
	overflow: hidden;
	font-family: $font-family-base;
	color: $clrBlack;
	font-size: 18px;
	font-weight: $fw-medium;
	@include media-breakpoint-down(xs) {
		font-size: 13px;
	}
}

p, dl, dt, dd {
	margin: unset;
}

dt {
	font-weight: unset;
}

.container {
	@include make-container;
	max-width: $container-max-width + $grid-gutter-width;
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	clip: rect(0 0 0 0);
	height: 1px;
	width: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
}

%col-ready {
	@include make-col-ready;
}
