.set {
	padding: 65px 0 40px;
	@include media-breakpoint-down(xs) {
		padding: 40px 0 50px;
	}
	&-title {
		margin-bottom: 40px;
		text-align: center;
	}
	&-img {
		position: relative;
		@include media-breakpoint-down(xs) {
			width: fit-content;
			margin: 0 auto;
		}
		&:before, &:after {
			position: absolute;
			display: block;
			font-weight: $fw-black;
			font-size: 20px;
			color: #FA4616;
			@include media-breakpoint-down(xs) {
				font-size: 16px;
			}
		}
		&_first {
			text-align: center;
			filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.3));
			&:before {
				content: '1';
				top: 95px;
				left: 105px;
				@include media-breakpoint-down(lg) {
					left: 50px;
				}
				@include media-breakpoint-down(sm) {
					left: 0;
				}
				@include media-breakpoint-down(md) {
					top: 190px;
				}
				@include media-breakpoint-down(xs) {
					top: 125px;
					right: 0;
					left: unset;
				}
			}
			&:after {
				content: '2';
				bottom: 20px;
				left: 105px;
				@include media-breakpoint-down(lg) {
					left: 50px;
				}
				@include media-breakpoint-down(sm) {
					left: 0;
				}
				@include media-breakpoint-down(xs) {
					right: 0;
					bottom: 0;
					left: unset;
				}
			}
			img {
				margin-left: 60px;
				@include media-breakpoint-down(lg) {
					margin-left: 0;
				}
			}
		}
		&_second {
			filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.3));
			@include media-breakpoint-down(md) {
				text-align: center;
			}
			&:before {
				content: '3';
				top: 95px;
				left: -40px;
				@include media-breakpoint-down(md) {
					top: 190px;
					left: 0;
				}
				@include media-breakpoint-down(xs) {
					top: 125px;
				}
			}
			&:after {
				content: '4';
				bottom: 100px;
				left: 0;
				@include media-breakpoint-down(xs) {
					bottom: 36px;
				}
			}
		}
		picture {

		}
		img {
			height: 400px;
			@include media-breakpoint-down(xs) {
				height: 230px;
			}
		}
		span {
			position: absolute;
			bottom: 10px;
			left: 0;
			color: #FA4616;
			font-weight: $fw-black;
			font-size: 20px;
			@include media-breakpoint-down(xs) {
				font-size: 16px;
				bottom: 3px;
			}
		}
	}
	&-list {
		margin-left: 20px;
		@include media-breakpoint-down(md) {
			margin: 20px auto 0;
			max-width: 265px;
		}
		@include media-breakpoint-down(xs) {
			padding-left: 40px;
		}
		&__item {
			font-size: 20px;
			line-height: 2.000em;
			letter-spacing: 0.03em;
			text-transform: uppercase;
			@include media-breakpoint-down(xs) {
				font-size: 16px;
			}
			span {
				margin-right: 10px;
				@include media-breakpoint-down(xs) {
					font-size: 16px;
				}
			}
			&:first-letter {
				color: red;
			}
		}
	}
}






