.steps {
	padding: 95px 0 110px;
	@include media-breakpoint-down(xs) {
		padding: 45px 0 40px;
	}
	&-title {
		text-align: center;
		margin-bottom: 40px;
		@include media-breakpoint-down(xs) {
			margin-bottom: 20px;
		}
	}
	&_mb {
		align-items: center;
		&:not(:last-child) {
			margin-bottom: 45px;
		}
		&:nth-of-type(3) img {
			object-fit: cover;
		}
	}
	&-invis {
		display: none;
		@include media-breakpoint-down(md) {
			display: block;
			font-size: 25px;
			text-transform: uppercase;
			text-align: center;
		}
		@include media-breakpoint-down(xs) {
			font-size: 13px;
		}
	}
	&-img {
		max-width: 359px;
		height: 233px;
		margin: 0 auto;
		box-shadow: -15px 25px 30px rgba(0, 0, 0, 0.1);
		border-radius: 40px;
		overflow: hidden;
		@include media-breakpoint-down(md) {
			margin: 20px auto 15px;
		}
		@include media-breakpoint-down(xs) {
			height: 188px;
		}
		picture, img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&-content {
		&__span {
			font-size: 25px;
			text-transform: uppercase;
			@include media-breakpoint-down(md) {
				display: none;
			}
		}
		&__text {
			max-width: 225px;
			margin-top: 10px;
			font-size: 16px;
			text-transform: uppercase;
			@include media-breakpoint-down(md) {
				max-width: 100%;
				text-align: center;
			}
			@include media-breakpoint-down(xs) {
				margin-top: 0;
			}
		}
	}
}