.mode {
	background: radial-gradient(72.12% 98.58% at 62.96% 22.23%, #F3F3F3 14.16%, #E1E1E1 100%);
	padding: 90px 0 50px;
	@include media-breakpoint-down(xs) {
		padding: 45px 0 0;
	}
	&-title {
		text-align: center;
		margin-bottom: 40px;
    @include media-breakpoint-down(xs) {
			margin-bottom: 35px;
		}
	}
	&-item {
		@include media-breakpoint-down(md) {
			margin-bottom: 50px;
		}
		&__img {
			position: relative;
			width: 185px;
			height: 385px;
			@include media-breakpoint-down(md) {
				margin-left: unset;
				margin: 0 auto;
			}
			@include media-breakpoint-down(xs) {
				width: 215px;
				height: 447px;
			}
			&_right {
				margin-left: auto;
				@include media-breakpoint-down(md) {
					margin-left: unset;
					margin: 0 auto;
				}
			}
			&_center {
				margin: 0 auto;
			}
			&:after {
				position: absolute;
				top: 35px;
				right: 10px;
				content: '';
				background-color: rgba(0, 0, 0, .3);
				filter: blur(20px);
				display: block;
				width: 195px;
				height: 370px;
				z-index: 0;
				@include media-breakpoint-down(xs) {
					width: 225px;
					height: 437px;
				}
			}
			picture {
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
				width: 100%;
				height: 100%;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
		&__content {
			margin-top: 40px;
			max-width: 180px;
			width: 100%;
			@include media-breakpoint-down(md) {
				margin-left: unset;
				margin: 40px auto 0;
			}
			&_right {
				margin-left: auto;
				@include media-breakpoint-down(md) {
					margin-left: unset;
					margin: 40px auto 0;
				}
			}
			&_center {
				margin: 40px auto;
				@include media-breakpoint-down(md) {
					margin: 40px auto 0;
				}
			}
		}
		&__wrap {
			display: flex;
			align-items: center;
			justify-content: space-between;
			&:first-child {
				margin-bottom: 5px;
			}
		}
		&__text {
			text-transform: uppercase;
		}
		&__svg {
			svg {
				width: 9px;
				height: 9px;
					&:not(:last-child) {
						margin-right: 2px;
					}
			}
		}
	}
}