.faq {
	&-title {
		margin-bottom: 30px;
		text-align: center;
		@include media-breakpoint-down(xs) {
			margin-bottom: 20px;
		}
	}
	&-list {
		max-width: 750px;
		margin: 0 auto;
			&__item {
			&:not(:last-child) {
				margin-bottom: 35px;
				@include media-breakpoint-down(xs) {
					margin-bottom: 13px;
				}
			}
		}
		&__title {
			margin-bottom: 20px;
			font-weight: $fw-medium;
			font-size: 20px;
			@include media-breakpoint-down(xs) {
				font-size: 13px;
				margin-bottom: 8px;
			}
		}
		&__text {
			font-weight: $fw-regular;
			line-height: 1.5em;
			letter-spacing: 0.01em;
			@include media-breakpoint-down(xs) {
				font-size: 11px;
			}
		}
	}
}
