.header-content {
  padding-top: 85px;
  @include media-breakpoint-down(xs) {
    padding-top: 310px;
  }
}

.hero-list {
  &__item {
    display: flex;
    align-items: center;
    font-size: 30px;
    color: $clrWhite;
    text-transform: uppercase;
    line-height: 2;
    letter-spacing: 0.05em;
    font-weight: $fw-regular;
    @include media-breakpoint-down(md) {
      font-size: 25px;
    }
    @include media-breakpoint-down(xs) {
      font-size: 13px;
      margin-bottom: 10px;
    }
    span {
      font-weight: $fw-semibold;
    }
  }
  svg {
    width: 29px;
    height: 29px;
    margin-right: 20px;
    @include media-breakpoint-down(xs) {
      width: 28px;
      height: 28px;
      margin-right: 10px;
    }
  }
}

