.overview {
	padding: 80px 0 105px;
	@include media-breakpoint-down(xs) {
		padding: 35px 0 40px;
	}
	.container {
		text-align: center;
	}
	&-content {
		align-items: center;
	}
	&-title {
	}
	&-subtitle {
		margin: 30px auto;
		max-width: 525px;
		font-size: 25px;
		text-transform: uppercase;
		letter-spacing: 0.03em;
		line-height: 1.400em;
    @include media-breakpoint-down(xs) {
			font-size: 13px;
			margin: 15px 0 35px;
		}
	}
	&-invis {
		display: none;
    @include media-breakpoint-down(sm) {
			display: block;
			position: absolute;
			top: 35%;
			right: 20%;
		}
    @include media-breakpoint-down(xs) {
			top: 25%;
			left: 67%;
			transform: translateX(-50%);
		}
		picture {
			&:first-child {
				margin-bottom: 10px;
			}
		}
	}
	&-img {
		height: 530px;
		width: 100%;
    @include media-breakpoint-down(xs) {
			height: 335px;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: 0 50%;
			@include media-breakpoint-down(md) {
				object-position: 50% 50%;
			}
		}
	}
	&-list {
		max-width: 390px;
		text-align: left;
    @include media-breakpoint-down(md) {
			margin: 25px auto 0;
		}
		&__item {
			display: flex;
			align-items: center;
			&:not(:last-child) {
				margin-bottom: 60px;
				@include media-breakpoint-down(xs) {
					margin-bottom: 20px;
				}
			}
			svg {
				width: 29px;
				height: 21px;
				margin-right: 40px;
				@include media-breakpoint-down(xs) {
					width: 12px;
					height: 9px;
					margin-right: 25px;
				}
			}
		}
		&__text {
			font-size: 20px;
			text-transform: uppercase;
			letter-spacing: 0.05em;
			@include media-breakpoint-down(xs) {
				font-size: 13px;
			}
		}
	}









}